.Navbar {
  /* FLEXBOX */
  display: flex;
  justify-content: space-between;
  position: absolute;
  z-index: 999;
  /* SIZE */
  width: 100%;
  height: 50px;
  /* STYLE */
  background-color: #397059;
  /* background-color: #d7b4c1; */
  color: #fcfcfc;
}

@media (max-width: 750px) {
  .Navbar {
    position: relative;
  }
}

